<template>
  <div class="payResult mescroll">
    <div class="bg">
      <img src="../../assets/img/pic/bg1.png" alt />
    </div>
    <div class="mid">
      <img src="../../assets/img/icon/success.png" alt />
      <p class="price">
        <span>¥</span>
        {{$route.query.payPrice?$route.query.payPrice.toFixed(2):'0.00'}}
      </p>
      <p class="state">支付成功</p>
      <p class="text">我们会尽快安排上架，请保持手机通讯畅通</p>
      <!-- <p class="text">以便快递哥哥第一时间联系到您。</p> -->
      <div class="orderInfo">
        <div class="flex">
          <div class="flex-1">订单编号</div>
          <div class="flex-2">{{$route.query.orderNo}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">支付方式</div>
          <div class="flex-2">微信</div>
        </div>
        <div class="flex">
          <div class="flex-1">支付时间</div>
          <div class="flex-2">{{getDate($route.query.payTime)}}</div>
        </div>
      </div>
      <div class="btn" @click="toDetail">查看订单</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    toDetail() {
      this.$router.replace({
        name: "orderDetail",
        query: {
          id: this.$route.query.orderId
        }
      });
    },
    getDate(time) {
      let year = new Date(time).getFullYear(),
        month = new Date(time).getMonth() + 1,
        day = new Date(time).getDate(),
        hour = new Date(time).getHours(),
        min = new Date(time).getMinutes(),
        sec = new Date(time).getSeconds();
      return (
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":" +
        (sec < 10 ? "0" + sec : sec)
      );
    }
  },
  mounted() {
    document.title = "支付结果";
  }
};
</script>
<style lang="scss" scoped>
.payResult {
  height: 100%;
  .bg {
    height: calc(100% - 92px);
    position: fixed;
    top: 42px;
    left: 0;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .mid {
    background: url("../../assets/img/pic/bg5.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    z-index: 1;
    text-align: center;
    margin: 0 30px;
    height: 75vh;
    padding: 50px 0 0;
    top: 10px;
    img {
      width: 60px;
      height: 60px;
    }
    .price {
      color: $base-color;
      font-weight: bold;
      font-size: 30px;
      padding: 5px 0;
      span {
        font-size: 18px;
        margin-right: 5px;
      }
    }
    .state {
      font-weight: bold;
      line-height: 30px;
    }
    .text {
      font-size: 13px;
      color: #999;
      line-height: 20px;
      text-align: center;
    }
    .orderInfo {
      margin: 50px 0 0;
      line-height: 35px;
      padding: 0 20px;
      font-size: 15px;
      .flex {
        display: flex;
        align-items: center;
        .flex-1 {
          width: 100px;
          color: #999;
          text-align: left;
        }
        .flex-2 {
          flex: 2;
          text-align: right;
        }
      }
    }
    .btn {
      background: $base-color;
      border-radius: 5px;
      height: 50px;
      line-height: 50px;
      color: #fff;
      margin: 20px 10px 0;
      font-size: 15px;
    }
  }
}
</style>
